* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
    list-style: none;
    font-family: 'PP Mori', sans-serif;
}

.header {
    width: 100%;
    top: 0;
    right: 0;
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 28px 8%;
    transition: all .50s ease;
}

.logoname {
    display: flex;
    align-items: center;
    cursor: url('../Assets//pointer.png'), auto;
}

.logoname span {
    color: black;
    font-weight: 300;
    font-size: 1.5rem;
}

.navbar {
    display: flex;
}

.navbar a {
    font-size: 1.1rem;
    padding: 5px 0px;
    margin: 0px 30px;
    transition: all .50s ease;
    color: black;
    font-weight: 400;
    cursor: url('../Assets//pointer.png'), auto;
}

.main {
    display: none;
}

.smallscreenmenu {
    border: none;
    background-color: transparent;
    font-size: 1.5rem;
    align-items: center;
    padding-top: 40%;
    cursor: pointer;
}

.navbar li {
    position: relative;
}

.nav-dot {
    position: absolute;
    top: 27px;
    right: 48px;
    width: 5px;
    height: 5px;
    background-color: transparent;
    border-radius: 50%;
    transition: all 0.50s ease-in-out;
}

.contact-nav-dot {
    right: 64px;
}

.navbar li a:hover ~ .nav-dot {
    background-color: black;
}

@media(max-width: 768px) {
    .main {
        display: block;
        transition: .2s;
    }

    .navbar {
        position: fixed; /* Fixed positioning */
        top: 80px; /* Align to top */
        right: -250px; /* Start off-screen, adjust this value to your preference */
        width: 200px;
        height: 160px; /* Full height */
        background: black;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        border-radius: 10px;
        transition: all 0.5s ease;
        overflow-x: hidden;
        z-index: 123456789;
        box-shadow: 2px 0 5px rgba(0, 0, 0, 0.5); /* Shadow effect */
    }
    

    .navbar.open {
        right: 60px; /* Slide in */
    }

    .navbar a {
        display: block;
        margin: 12px 0;
        padding: 0px 25px;
        transition: all 0.5s ease;
        color: white;
        font-weight: 300;
        padding-top: 5px;
    }

    .navbar ul li a {
        font-weight: 400;
    }

    .nav-dot {
        position: absolute;
        top: 27px;
        right: 48px;
        width: 5px;
        height: 5px;
        background-color: transparent;
        border-radius: 50%;
        transition: all 0.50s ease-in-out;
    }

    .navbar li a:hover ~ .nav-dot {
        background-color: white;
    }
}

@media only screen and (max-width: 416px) and (min-width: 385px) {
    .main {
        display: block;
        transition: .2s;
    }

    .navbar {
        position: fixed; /* Fixed positioning */
