        right: 60px; /* Slide in */
    }

    .navbar a {
        display: block;
        margin: 12px 0;
        padding: 0px 25px;
        transition: all 1s ease;
        color: white;
        font-weight: 300;
        padding-top: 5px;
    }

    .navbar ul li a {
        font-weight: 400;
    }

    .nav-dot {
        position: absolute;
